/* font-family: 'Barlow', sans-serif;
font-family: 'Offside', cursive; */


header {
  background-image: url(/project6CollegeNavigator/static/media/header.dacb15c2.jpg);
  background-size: cover;
  min-height: 40vh;
  background-position: left;
  display:flex;
  flex-direction: column;
  justify-content: flex-end;
  
}

h1 {
  font-family: 'Offside', cursive;
  color:#073330;
  font-size: 7rem;
  margin-bottom: 10%;
  background: #416aff59;
  padding: 20px;
  text-shadow: 2px 2px 2px #b1bc2b  ;
}

body {
  font-family: 'Barlow', sans-serif;
  font-size: 1.8rem;
}

form {
  background-color: #073330bb;
  color: white;
  padding: 20px;
  display:flex;
  flex-direction: column;
  font-weight: 500;
}

.cityInputs, .radiusInputs, .schoolTypeInputs {
  margin: 15px 0;
}

input[type="text"] {
  margin:0 5px;
  width: 300px;
  padding: 10px;
}

input[type="radio"] {
  margin:10px 20px;
}

header button {
  width: 20%;
  padding: 10px;
  background-color:white;
  color: #073330;
  font-weight: 700;
  border:#b1bc2b;
}

nav {
  background-color:#b1bc2b ;
  color: #073330;
  padding: 15px;
}

nav ul {
  list-style: none;
  display: flex;
  justify-content: space-evenly;
}

a {
  text-decoration: none;
  font-weight: 700;
  color: #416bff;
}

details {
  margin: 20px;
}

details p{
  display:flex;
  flex-direction: column;
  margin: 10px;
  color:#073330;
  
}

summary {
  color:#073330;
  font-weight: 700;
}

.schoolResults button {
  margin: 10px;
  margin-left: 8%;
  padding: 5px;
  background-color: #073330;
  color:white;
  border:none;
  width: 200px;
}

.schoolResults {
  border:2px solid #b1bc2b;
  min-width:440px;
  width: 30%;
  display: flex;
  flex-direction: column;
  margin: 10px 0;
}
